/* Large Devices, Wide Screens */
@media only screen and (max-width : 1199px) {
  .catalog {
    &__block {
      max-width: 212px; }
    &__carousel {
      &__block {
        width: 212px; } }
    &__navigation {
      nav {
        ul {
          ul {
            li {
              &>a {
                padding-right: 55px;
                span {
                  width: 50px; } } } } } } } }
  .news {
    &__img {
      width: 213px; } }

  .private {
    &__form {
      width: 294px; }
    &__terms {
      width: 374px;
      height: 360px; } }

  body {
    padding-right: 0 !important; }

  .div-date-table {
    max-width: 120px;
    .div-date--day, .div-date--month {
      width: 50%; }
    .div-date--time {
      width: 100%; }
    .form-group {
      margin-bottom: 5px; } }

  .ordering {
    .input-label {
      small {
        white-space: normal; } }
    .form-control--address {
      width: 100%;
      margin-left: 0; }
    &__total {
      &>div {
        text-align: left;
        span {
          display: block; }
        strong {
          padding-left: 0; } } } }

  .product {
    &__gallery {
      &__left {
        width: 66px;
        height: 477px;
        padding: 40px 0; }
      &__center {
        margin-left: 80px;
        width: 455px;
        height: 606px; } }
    &__zoom {
      display: none; } }

  .filter {
    &__row {
      padding-right: 40px;
      .sup {
        width: 35px; }
      &--color {
        padding-right: 30px;
        .sup {
          right: 12px; } } } }

  .collection {
    &__carousel {
      padding: 0 50px; }
    &__frame {
      width: 294px;
      height: 396px;
      background-size: 294px 396px;
      &__name {
        bottom: 80px;
        left: 25px; }
      &__label {
        right: -5px;
        top: 155px; } } } }

/* Medium Devices, Desktops */
@media only screen and (max-width : 991px) {
