      display: block;
      text-align: center;
      &>div {
        display: block;
        &+div {
          padding: 15px 0 0 0; } }
      p {
        margin: 0 -5px 0 -5px; } }
    &__icon {
      margin-left: auto;
      margin-right: auto; } }

  .catalog {
    &__tabs {
      a {
        padding-left: 25px;
        padding-right: 25px; } }
    &__block {
      max-width: 220px; }
    &__carousel {
      &__block {
        width: 220px; } } }

  .news {
    &__img {
      width: 157px; } }

  .private {
    &__cabinet.table {
      display: block;
      &>.table-cell {
        width: 100% !important;
        display: block;
        &+.table-cell {
          padding-left: 0; } } }
    &__form {
      width: 100%; }
    &__terms {
      width: 100%; }
    &__discount {} }

  .menu {
    &__li1 {
      &>a {
        padding: 0 10px; } } }

  .ordering {
    &__total {
      margin: 0;
      padding: 20px 10px;
      text-align: center;
      &>div {
