  .product {
    &__info {
      margin-left: 20px; }
    &__gallery {
      &__left {
        width: 63px;
        height: 461px; }
      &__center {
        width: 360px;
        height: 480px; } } }

  .filter {
    &__row {
      padding-right: 10px;
      .sup {
        display: none; }
      &--color {
        padding-right: 30px;
        .sup {
          right: 10px;
          display: block; } } } } }

@media (min-width: 768px) and (max-width: 991px) {
