    &__frame {
      width: 220px;
      height: 296px;
      top: 36px;
      background-size: 220px 296px;
      &__name {
        bottom: 50px;
        left: 18px; }
      &__label {
        right: -12px;
        top: 133px; } } } }


/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {
  .advantages {
    &__block {
      max-width: 200px;
      margin: 0 auto; }
    .row {
      &>div {
        &:first-child, &:nth-child(2) {
          .advantages__block {
            margin-bottom: 20px; } } } } }

  .section {
    padding: 30px 0; }

  .footer {
    padding-top: 40px;
    padding-bottom: 40px;
    font-size: rem(15);
    &__title {
      font-size: rem(21);
      font-weight: 600; }
    ul {
      margin-bottom: 20px;
      li {
        a {
          &::after {
            font-size: rem(14); } } } } }
  .copyright {
    text-align: center;
    font-size: rem(14);
    max-width: 320px;
    margin: 0 auto 0; }

  .header {
    &__top {
      text-align: center; }
    &__contacts {
      float: none;
      display: inline-flex;
      justify-content: flex-end;
      width: 258px;
      &>div {
        &.hide {
          display: none !important; } }
      &.is-active {
        color: $primary-color;
        &>div {
          &.show {
            width: 0;
            overflow: hidden;
            padding: 0; }
          &.hide {
            display: block !important; } }
        .btn {
          color: $primary-color; } } }

    &__bottom {
      padding-top: 60px; }
    &__social, &__links {
      margin-top: 20px; } }

  .btn {
    &--feedback {
      max-width: 480px;
      margin: 0 auto;
      float: none;
      display: block; } }
  .catalog {
    &__tabs {
      float: none;
      margin: 0 0 25px 0; }
    &__navigation {
      &.is-active {
        button[type="button"] {
          i {
            transform:  translateY(-50%) rotate(0deg); } } }
      button[type="button"] {
        cursor: pointer;
        i {
          transform-origin: 50% 50%;
 } }          // transform:  translateY(-50%) rotate(-180deg)
      nav {
        // border: 1px solid $color-s-lightbrown
        display: none;
        box-shadow: 0 18px 50px -2px rgba(0, 0, 0, 0.2); } }
    &__block {
      max-width: 260px; }
    &__carousel {
      .catalog__block {
 } } }        // max-width: 210px

  .news {
    &__img {
      width: 130px; }
    &__row {
      p {
        display: none; } } }

  .search__block {
    left: 50px !important;
    right: 50px !important;
    top: 110px !important;
    width: auto !important; }

  .menu {
    &__mobile {
      margin-top: -50px; } }

  .ordering {
    .form-control--address {
      margin-left: 25px;
      width: calc(100% - 25px);
      max-width: 400px; }
    &__total {
      &>div {
        display: block;
        margin-right: 0;
        margin-bottom: 10px;
        text-align: center; }
      .btn {
        max-width: 230px; } } }

  .div-date-table {
    max-width: 240px; }
  .div-date-table .div-date--day, .div-date-table .div-date--month {
    width: 25%; }
  .div-date-table .div-date--time {
    width: 50%; }

  .form {
    &-control {
      &--counter {
        margin-left: 0; } } }

  .cart {
    margin-bottom: 30px;
    &__links {
      white-space: nowrap; }
    &>table {
      &>tbody {
        &>tr {
          &>td {
            h4, .h4 {
              margin-bottom: 0;
              line-height: 1.2; } } } }
      &>thead {
        &>tr {
          &>th {
            &.cart {
              &__th {
                &1 {
                  width: 25px; }
                &7 {
                  width: auto; } } } } } } }
    &__total {
      float: none;
      width: 100%;
      &>div {
        display: block;
        margin-right: 0;
        margin-bottom: 10px;
        text-align: center; }
      .btn {
        max-width: 230px;
        margin-left: auto;
        margin-right: auto;
        display: block; }
      &--order-list {
        .btn {
          float: none;
          margin-top: 0; }
        &>div {
          float: none; } } }

    .table {
      margin-bottom: 5px !important;
      td {
        padding-top: 0 !important;
        padding-bottom: 0 !important; } } }

  .cart > table > tbody > tr > td.cart__td7 {
    white-space: nowrap; }
  .cart > table > thead > tr > th {
    font-size: rem(14);
    strong {
      color: $primary-color; } }

  .order-list__row {
    max-height: 149px; }

  .product {
    &__info {
      margin: 0 auto; } }

  table.table {
    margin-bottom: 15px; }

  .product {
    width: 450px;
    margin-left: auto;
    margin-right: auto;
    &__gallery {
      &__left {
        width: 60px;
        height: 442px;
        ul {
          li {
            margin-bottom: 10px; } } }
      &__center {
        width: 355px;
        height: 480px;
        margin-left: 75px; } } }

  .filter {
    &__body {
      display: none;
      position: absolute;
      width: 100%;
      padding: 0 13px 13px 13px;
      box-shadow: 0 0px 40px 0 rgba(0, 0, 0, 0.2); }
    &__block {
      margin-left: -13px;
      margin-right: -13px;
      width: calc(100% + 26px) !important; }
    &__row {
      padding-right: 55px;
      .sup {
        width: 50px;
        display: block; }
      &--color {
        .sup {
          right: 20px; } } }
    &__head {
      &--top {
        margin-left: -13px;
        margin-right: -13px; } } }

  .collection {
    &__carousel {
      padding: 0 0px;
      .slick-slide a img {
        max-width: 290px; } }
    &__frame {
      width: 355px;
      height: 478px;
      background-size: 355px 478px;
      &__name {
        bottom: 100px;
        left: 30px; }
      &__label {
        right: 1px;
        top: 170px; } } }

  .filter {
    margin-bottom: 25px; } }

@media only screen and (max-width: 599px) {
  .header {
    &__social {
      text-align: left; }
    &__links {
      text-align: right; } } }

/* Extra Small Devices, Phones */
@media only screen and (max-width : 479px) {
  .col-xxs-12 {
    width: 100% !important;
    float: none !important; }
  .hidden-xxs {
    display: none !important; }
  .visible-xxs {
    display: block !important; }

  .advantages {
    .row {
      &>div {
        &:not(:last-child) {
          .advantages__block {
            margin-bottom: 20px; } } } } }
  .footer {
    padding-bottom: 35px;
    .col-xxs-12 {
      max-width: 244px;
      margin: 0 auto; } }

  .logo {
    max-width: 224px; }

  .catalog {
    &__tabs {
      max-width: 290px;
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      ul {
        display: inline-block; }
      a {
        padding-left: 15px;
        padding-right: 15px; } }
    &__carousel {
      &__block {
        margin-right: 0; } }
    &__sort {
      float: none !important;
      &+.catalog__sort {
        margin-top: 5px; } } }

  .news {
    &__row {
      &>div {
        &+div {
          padding-left: 0; } }
      .btn {
        margin-left: auto;
        margin-right: auto;
        display: block; } } }

  .pagination {
    // margin-bottom: 15px
    &>div {
      &:first-child, &:last-child {
        display: none; }
      &:nth-child(2) {
        padding-left: 0;
        padding-right: 0;
        text-align: center; } }
    ul {
      li + li {
        padding-left: 10px; } } }

  .search__block {
    left: 10px !important;
    right: 10px !important; }

  .menu {
    &__mobile {
      margin-top: 0px; } }

  .cart > table > thead > tr > th, .cart > table > thead > tr > td, .cart > table > tbody > tr > th, .cart > table > tbody > tr > td {
    padding-left: 2px;
    padding-right: 2px; }
  .cart > table > tbody > tr > td h4, .cart > table > tbody > tr > td .h4 {
    font-size: rem(15); }

  .private {
    &__discount {
      &--480 {
        float: none;
        margin-top: 0;
        width: 100%;
        .table {
          width: 100%;
          &>div {
            width: auto !important; } }
        b {
          br {
            display: none; } }
        strong {
          float: right;
          height: 30px;
          line-height: 30px;
          width: 30px;
          font-size: rem(15); } } } }

  .catalog {
    &__viewed {
      .catalog__block {
        max-width: 207px; }
      .slick-next, .slick-prev {
        display: none !important; } } }

  .product {
    width: 290px;
    margin-left: auto;
    margin-right: auto;
    &__gallery {
      margin-bottom: 15px;
      &__left {
        width: 55px;
        height: 362px;
        padding: 30px 0;
        ul {
          li {
            margin-bottom: 5px; } }
        .slick-prev {
          top: -42px; } }
      &__center {
        width: 230px;
        height: 310px;
        margin-left: 60px;
        position: relative;
        top: 30px; } } }

  .collection {
    margin: 0;
    padding: 0;
    &__carousel {
      padding: 0;
      margin: 0;
      .slick-slide a img {
        max-width: 215px; } }
    &__frame {
      width: 250px;
      height: 336px;
      background-size: 250px 336px;
      &__name {
        bottom: 67px;
        left: 20px; }
      &__label {
        right: -9px;
        top: 145px; } } }

  .section__head .h1 .small, .section__head h1 .small {
    right: 0; } }

